import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import { BreadCrumbs } from '../components/breadcrumbs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FormattedMessage, Link, useIntl } from 'gatsby-plugin-intl';

const ContactPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile } = data;
  const firm = allFile.edges.find((v) => v.node.name === 'firm').node.childImageSharp.fluid;
  const services = allFile.edges.find((v) => v.node.name === 'services').node.childImageSharp.fluid;
  const commitment = allFile.edges.find((v) => v.node.name === 'commitment').node.childImageSharp.fluid;

  function renderContent(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <Img fluid={firm} className="img-fluid" alt="Firm" />
              </Col>
              <Col>
                <h4>Notre Entreprise</h4>
                <p>
                  Aviaco est une société multinationale axée sur la fourniture et la gestion d&apos;équipements de GSE pour le secteur de
                  l&apos;aviation.
                </p>
                <p>
                  Aviaco est devenue l&apos;une des plus grandes sociétés au monde fournissant des équipements de GSE usagés et remis à neuf
                  dans plus de 90 pays à travers le monde.
                </p>
                <p>
                  Notre entreprise dispose d&apos;une solide équipe de professionnels de haut niveau, qui ont plus de 25 ans
                  d&apos;expérience dans le secteur du GSE, ce qui a conduit l&apos;entreprise à être l&apos;un des exploitants clés sur le
                  marché GSE de nos jours.
                </p>
                <p>
                  Notre société fournit un large éventail de services à nos clients, notamment la vente et l&apos;achat d&apos;équipement de
                  GSE dans le monde entier; services de location de GSE à court et à long terme; services de remise à neuf, de réparation et
                  d&apos;entretien; la gestion de la flotte et d&apos;autres services connexes à valeur ajoutée.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h4>Nos principaux services</h4>
                <h5>Vente et achat</h5>
                <p>
                  Notre vente et achat d&apos;équipement de GSE de haute qualité dans le monde entier, surtout en Afrique, en Asie et en
                  Europe, est capable de satisfaire toute demande de clients grâce à notre réseau mondial de fournisseurs GSE et notre vaste
                  expérience dans l&apos;industrie aéronautique.
                </p>
                <h5>Réparation et entretien</h5>
                <p>
                  La réparation et l&apos;entretien sont exécutés dans nos propres ateliers principaux en Europe (Pays-Bas et Espagne) et
                  dans notre réseau de sous-traitants de haute qualité. Ils sont tous capables de réviser tout type d&apos;équipement.
                </p>
                <h5>Location courte et longue durée</h5>
                <p>
                  Nous en louons GSE depuis quelques années à court et long terme dans 5 pays européens. Ceci offre plusieurs solutions et
                  avantages à nos clients. Nous sommes prêts à étendre notre offre de location dans le monde entier.
                </p>
                <h5>Entretien de la flotte</h5>
                <p>
                  La gestion de la flotte est équipée d&apos;un système de surveillance performante, qui nous permet immédiatement de
                  fournir à nos clients des informations pertinentes sur la disponibilité.
                </p>
              </Col>
              <Col>
                <Img fluid={services} className="img-fluid" alt="Services" />
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={commitment} className="img-fluid" alt="Commitment" />
              </Col>
              <Col>
                <h4>Notre engagement</h4>
                <p>
                  La mission d&apos;Aviaco est de créer une relation d&apos;affaires ouverte et réelle avec nos clients, où la confiance est
                  essentielle. Nous nous engageons fermement à toujours garder une politique de «livres ouverts» en termes de qualité de
                  service et de prix.
                </p>
                <p> Cet engagement est basé sur les paramètres suivants:</p>
                <ul>
                  <li>
                    Transparence <br />
                    Pas de coûts cachés dans nos offres
                  </li>
                  <li>
                    Adéquation <br />
                    Toujours citer les types d&apos;équipements qui répondent le mieux aux besoins de nos clients
                  </li>
                  <li>
                    Empathie <br />
                    Penser avec nos clients et leur apporter soutien et feedback
                  </li>
                  <li>
                    Facilitation <br />
                    Proposer des services à valeur ajoutée (transport, mise en service sur site, ..)
                  </li>
                  <li>
                    Réalisation <br />
                    Se concentrer sur la satisfaction de toutes les attentes de nos clients
                  </li>
                </ul>
              </Col>
            </Row>
          </Container>
        );
      case 'es':
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <Img fluid={firm} className="img-fluid" alt="Firm" />
              </Col>
              <Col>
                <h4>Nuestra Compañia</h4>
                <p>
                  Aviaco es una compañía multinacional centrada en el suministro y la gestión de equipos de apoyo en tierra (GSE) para el
                  sector de la aviación.
                </p>
                <p>
                  Después de ser un elemento clave en el mercado de GSE durante décadas, Aviaco se ha convertido en una de las mayores
                  empresas del sector que suministra equipos de soporte terrestre usados ​​y reacondicionados en más de 90 países en todo el
                  mundo.
                </p>
                <p>
                  Aviaco cuenta con un sólido equipo de profesionales de primer nivel, con más de 25 años de experiencia en el sector, lo
                  que ha llevado a la compañía a ser uno de los principales actores en el mercado de GSE en la actualidad.
                </p>
                <p>
                  Nuestra compañía ofrece una amplia cartera de servicios para nuestros clientes que incluye ventas y compra de equipos de
                  soporte en tierra en todo el mundo; servicios de alquiler de GSE a corto y largo plazo; servicios de restauración y
                  reparación y mantenimiento; gestión de flotas y otros servicios relacionados de valor agregado.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h4>Nuestros principales servicios</h4>
                <h5>Compra y vento</h5>
                <p>
                  Venta y compra de equipos de soporte terrestre de alta calidad en todo el mundo, especializados en África, Asia y Europa,
                  y capaces de satisfacer cualquier demanda de nuestros clientes gracias a nuestra red global de proveedores de GSE y
                  nuestra vasta experiencia en la industria de la aviación.
                </p>
                <h5>Reparacion &amp; Mantenimiento </h5>
                <p>
                  Realizamos mantenimiento y reacondicionamiento de nuestros equipos en principales talleres en Europa (Países Bajos y
                  España) y a través de nuestra red de sub contratistas que cuentan con instalaciones de alto nivel capacesde revisar y
                  reparar todo tipo de equipos. Asimismo prestamos servicios de reacondicionamiento de equipos de nuestros clientes a fin de
                  extender su vida útil a los precios más competitivos del mercado.
                </p>
                <h5> Alguiler a corto y largo plazo</h5>
                <p>
                  Alquiler de GSE a corto y largo plazo: ofrecemos diversas soluciones de arrendamiento de equipos a nuestros clientes ya
                  existentes en 5 países de Europa y estamos listos para ampliar nuestra oferta a todo el mundo.
                </p>
                <h5>Gestion de flota</h5>
                <p>
                  Gestión de flota, contando con un sólido sistema de supervisión que nos permite proporcionar datos relevantes en términos
                  de necesidad y disponibilidad de unidades a nuestros clientes.
                </p>
              </Col>
              <Col>
                <Img fluid={services} className="img-fluid" alt="Services" />
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={commitment} className="img-fluid" alt="Commitment" />
              </Col>
              <Col>
                <h4>Nuestro compromiso</h4>
                <p>
                  La misión de Aviaco es crear una relación comercial abierta y verdadera con nuestros clientes, donde la confianza es
                  esencial, por tanto estamos fuertemente comprometidos a mantener siempre una política de "libros abiertos" en términos de
                  calidad de servicio y fijación de precios.
                </p>
                <p> Este compromiso se basa en los siguientes parámetros:</p>
                <ul>
                  <li>
                    Transparencia <br />
                    Sin costes ocultos en nuestras ofertas
                  </li>
                  <li>
                    Idoneidad <br />
                    Ofertando siempre los tipos de equipos que satisfacen mejor las necesidades de nuestros clientes{' '}
                  </li>
                  <li>
                    Empatía <br />
                    Pensar con nuestros clientes y brindarles apoyo y sugerencias
                  </li>
                  <li>
                    Facilitación <br />
                    Ofreciendo otros servicios de valor añadido(transporte, trabajos en la ubicación del cliente, etc.
                  </li>
                  <li>
                    Cumplimiento <br />
                    Nuestro objetivo es satisfacer todas las expectativas de nuestros clientes
                  </li>
                </ul>
              </Col>
            </Row>
          </Container>
        );
      default:
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <Img fluid={firm} className="img-fluid" alt="Firm" />
              </Col>
              <Col>
                <h1>Full-service providers for GSE</h1>
                <p>
                  Aviaco GSE is a specialist in Ground Support Equipment (GSE) for the aviation sector. Working with us means working with a
                  select team of professionals. Experts who offer you every service you need to keep your airport running smoothly. Whether
                  you need to buy or rent new or second-hand equipment, help with fleet management, maintenance or repairs, or would simply
                  like a little insight and friendly advice, Aviaco is always at your service. Contact us today!
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={services} className="img-fluid" alt="Services" />
              </Col>
              <Col>
                <h2>Experience with GSE</h2>
                <p>
                  Aviaco is built on experience. The company has grown to become a full-service provider in the field of Ground Support
                  Equipment (GSE). You’ll not only find new and second-hand GSE for sale on our website, you’re also able to call on us to
                  lease GSE for the short and long term, rely on our fleet management services and even have your GSE maintained and
                  repaired by our qualified technicians.
                </p>
                <h3>Our values</h3>
                <p>
                  At Aviaco, we create and nurture open, honest business relationships with our customers. We take an open-book approach to
                  service quality and pricing. Working with Aviaco, you benefit from:
                  <ul>
                    <li>
                      <strong>Transparency</strong>
                      <br />
                      No hidden costs.
                    </li>
                    <li>
                      <strong>Suitability</strong>
                      <br />
                      We advise and provide quotes on the equipment that best meets your needs.
                    </li>
                    <li>
                      <strong>Empathy</strong>
                      <br />
                      We look at things from your perspective, providing advice, support and targeted feedback.
                    </li>
                    <li>
                      <strong>Facilitation</strong>
                      <br />
                      Your needs are met through our value-added services such as transport, on-site commissioning and maintenance.
                    </li>
                    <li>
                      <strong>Fulfillment</strong>
                      <br />
                      Your needs and expectations are our focus.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={commitment} className="img-fluid" alt="Commitment" />
              </Col>
              <Col>
                <h3>Contact us</h3>
                <p>
                  Would you like to discuss your GSE needs with us?
                  <br />
                  Are you looking for a particular piece of GSE equipment?
                  <br />
                  <Link to="/contact">
                    <strong>Contact us today.</strong>
                  </Link>
                  We look forward to helping you.
                </p>
              </Col>
            </Row>
          </Container>
        );
    }
  }

  return (
    <Layout>
      <SEO
        title={intl.formatMessage({ id: 'about-seo-title' })}
        description={intl.formatMessage({ id: 'about-seo-description' })}
        keywords={intl.formatMessage({ id: 'about-seo-keyword' })}
      />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="about-us" />
                  <h1 className="page-title">
                    <FormattedMessage id="about-aviaco" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="about-aviaco-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          {renderContent(intl.locale)}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allFile(filter: { name: { in: ["firm", "services", "commitment"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default ContactPage;
