import React from 'react';
import { FormattedMessage, Link } from 'gatsby-plugin-intl';
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import { AllorsProductCategory, AllorsUnifiedGood } from '../../graphql-types';

export const BreadCrumbs: React.FunctionComponent<{
  section?: string;
  family?: AllorsProductCategory;
  group?: AllorsProductCategory;
  product?: AllorsUnifiedGood;
}> = ({ section, family, group, product }) => {
  return (
    <Breadcrumb>
      <li className="breadcrumb-item">
        <Link to="/">
          <FormattedMessage id="home" />
        </Link>
      </li>

      {section && !(family || group) && (
        <Breadcrumb.Item active>
          <FormattedMessage id={section} />
        </Breadcrumb.Item>
      )}
      {section && (family || group) && (
        <li className="breadcrumb-item">
          <Link to={`/${section}`}>
            <FormattedMessage id={section} />
          </Link>
        </li>
      )}

      {family && !group && <Breadcrumb.Item active>{family.name}</Breadcrumb.Item>}
      {family && group && (
        <li className="breadcrumb-item">
          <Link to={`/${family.slug}`}>{family.name}</Link>
        </li>
      )}

      {group && !product && <Breadcrumb.Item active>{group.name}</Breadcrumb.Item>}
      {group && product && (
        <li className="breadcrumb-item">
          <Link to={`/${group.slug}`}>{group.name}</Link>
        </li>
      )}

      {product && <Breadcrumb.Item active>{product.name}</Breadcrumb.Item>}
    </Breadcrumb>
  );
};
